.popup {
  $this: &;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 20;
  background-color: rgba(#000, .7);
  @include flex(center, center);
  visibility: hidden;
  opacity: 0;
  @include tr(opacity .3s, visibility .3s);
  &.is-active {
    visibility: visible;
    opacity: 1; }
  &__inner {
    width: 100%; }
  &__close {
    display: inline-block;

    top: 10px;
    right: 10px;
    @include icon-close(21, 2, #858585);
    position: absolute;
    background-color: #f4f4f4;
    border-radius: 50%;
    &::before,
    &::after {
      height: 15px;
      top: 3px; } }

  &--form {
    #{$this}__inner {
      background-color: rgba(#f4f4f4, .7);
      width: 350px; }
    #{$this}__title {
      padding-top: 8px;
      padding-bottom: 8px;
      background-color: #464545;
      padding-right: 5px;
      padding-left: 15px;
      position: relative;
      .h3, h3 {
        text-align: left;
        margin-bottom: 0; } }
    #{$this}__form {
      padding: 15px; } } }
