.popup { position: fixed; width: 100%; height: 100%; top: 0; left: 0; z-index: 20; background-color: rgba(0, 0, 0, 0.7); display: flex; justify-content: center; align-items: center; flex-direction: row; flex-wrap: nowrap; visibility: hidden; opacity: 0; transition: opacity 0.3s, visibility 0.3s; }

